import userList from '@/components/index/userList';
export default {
  data() {
    return {
      userId: '',
      userType: PARENT,
      chooseType: '宝宝'
    }
  },
  created() {
    // //获取localStorage
    this.userList = JSON.parse(localStorage.getItem('USERLIST'));
    this.userInfo = JSON.parse(sessionStorage.getItem('CURRENT_USER'));
    if (!this.userInfo) {
      this.userId = this.userList.list[0].parentId;
      this.setSessionStorage(this.userList.list[0]);
    } else {
      this.userId = this.userInfo.parentId;
    }
  },
  methods: {
    getUserList() {
      this.$refs.userList.setShowFlags();
    },
    getData(data) {
      this.userId = data.parentId;
      this.setSessionStorage(data);
    },
    setSessionStorage(user) {
      sessionStorage.setItem('CURRENT_USER', JSON.stringify(user));
    }
  },
  components: {
    userList
  }
};



